/* ==========================================================================
   #RESET
   ========================================================================== */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* ==========================================================================
   #BASE STYLES
   ========================================================================== */
html {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@-ms-viewport {
  width: device-width;
}

body {
  width: 100%;
  min-width: 320px;
  background-color: #fff;
  overflow-x: hidden;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

sup {
  position: relative;
  top: -.5em;
  font-size: 60%;
  line-height: 0;
  vertical-align: baseline;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

b {
  font-weight: bolder;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.25;
}

input, textarea, select, button {
  color: inherit;
  font-family: inherit;
}

.container {
  width: 100%;
}

@media (max-width: 599.98px) {
  .container {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}

@media (min-width: 600px) {
  .container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 900px) {
  .container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1200px) {
  .container {
    padding-left: 4rem;
    padding-right: 4rem;
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1320px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

.logo {
  display: block;
  text-decoration: none;
}

.logo:hover {
  text-decoration: none;
}

.logo--header {
  width: 119px;
  margin-right: auto;
}

@media (min-width: 1200px) {
  .logo--header {
    width: 165px;
  }
}

.logo--footer {
  width: 134px;
}

@media (max-width: 599.98px) {
  .logo--footer {
    margin-bottom: 2.125rem;
  }
}

@media (min-width: 600px) {
  .logo--footer {
    margin-right: auto;
  }
}

@media (min-width: 1200px) {
  .logo--footer {
    width: 191px;
  }
}

.hamburger {
  display: inline-block;
  width: 44px;
  height: 44px;
  padding-left: 4.5px;
  padding-right: 4.5px;
  border: none;
  outline: none;
  background-color: transparent;
  transition: opacity 0.2s ease-in-out;
  cursor: pointer;
}

@media (max-width: 599.98px) {
  .hamburger {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.hamburger:hover, .hamburger:focus {
  opacity: 0.7;
}

.hamburger-icon {
  pointer-events: none;
}

.link-underline {
  border-bottom: 1px solid currentColor;
  text-decoration: none;
  transition: border-color 0.2s ease-in-out;
}

.link-underline:hover {
  text-decoration: none;
  border-color: rgba(0, 0, 0, 0);
}

.highlight {
  color: #8c2c3a;
}

.title {
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  font-size: calc(1rem + 5.3125vw);
}

@media (min-width: 600px) {
  .title {
    font-size: calc(1rem + 3.194vw);
  }
}

@media (min-width: 1440px) {
  .title {
    font-size: 3.875rem;
  }
}

.title--smaller {
  display: block;
  margin-top: 1rem;
  font-weight: normal;
  font-size: calc(1rem + 0.625vw);
}

@media (min-width: 600px) {
  .title--smaller {
    font-size: calc(1rem + 0.97vw);
  }
}

@media (min-width: 1440px) {
  .title--smaller {
    margin-top: 0.375rem;
    font-size: 1.875rem;
  }
}

/* ==========================================================================
   #BASIC TYPOGRAPHY STYLES
   ========================================================================== */
html {
  font-size: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-weight: normal;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.125rem;
  font-size: calc(1rem + 0.625vw);
  line-height: 1.22;
  color: #39434d;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

/* ==========================================================================
   #HELPERS
   ========================================================================== */
.hidden {
  display: none !important;
}

/*
* Hide only visually, but have it available for screen readers
*/
.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/*
* Extends the .sr-only class to allow the element
* to be focusable when navigated to via the keyboard
*/
.sr-only.focusable:active,
.sr-only.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
  white-space: inherit;
}

/*
* Hide visually and from screen readers, but maintain layout
*/
.invisible {
  visibility: hidden;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

/* ==========================================================================
   #MENU
   ========================================================================== */
.menu {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}

/*------------------------------------------*/
/*  BUTTONS
/*------------------------------------------*/
.btn {
  position: relative;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: top;
  user-select: none;
  transition: background-color 0.15s ease-in-out;
  cursor: pointer;
}

.btn:focus {
  outline: none;
}

/* ==========================================================================
   #BULLET-LIST
   ========================================================================== */
.bullet-list > * {
  display: flex;
  align-items: baseline;
}

.bullet-list > *::before {
  content: '';
  position: relative;
  top: -0.08em;
  flex: none;
  display: block;
  width: 15px;
  height: 15px;
  margin-right: 30px;
  background-color: #8c2c3a;
}

.radial-progress {
  position: relative;
  width: 272px;
  height: 272px;
  background-color: rgba(140, 44, 58, 0.2);
  border-radius: 50%;
}

.radial-progress__mask, .radial-progress__fill, .radial-progress__shadow {
  width: 272px;
  height: 272px;
  position: absolute;
  border-radius: 50%;
}

.radial-progress__shadow {
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.25) inset;
}

.radial-progress__mask, .radial-progress__fill {
  backface-visibility: hidden;
  transition: transform 1s;
  border-radius: 50%;
}

.radial-progress__mask {
  clip: rect(0px, 272px, 272px, 136px);
}

.radial-progress__mask .radial-progress__fill {
  clip: rect(0px, 136px, 272px, 0px);
  background-color: #8c2c3a;
}

.radial-progress__inset {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 196px;
  height: 196px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.25);
}

.radial-progress__percentage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1;
}

.radial-progress__numbers span {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  font-weight: bold;
  font-size: 58px;
}

