  border-top: 18px solid #39434d;
  border-left: 18px solid transparent;
  border-right: 18px solid transparent;
}

@media (min-width: 1200px) {
  .drugs-item::after {
    border-top: 28px solid #39434d;
    border-left: 28px solid transparent;
    border-right: 28px solid transparent;
  }
}

.drugs-item__more {
  display: inline-block;
  margin-top: 0.9375rem;
}

@media (min-width: 1200px) {
  .drugs-item__more {
    margin-top: 2rem;
  }
}

.drugs-item__top {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 190px;
  height: 190px;
  overflow: hidden;
}

@media (min-width: 1200px) {
  .drugs-item__top {
    width: 250px;
    height: 125px;
    transform: translate(-50%, -100%);
  }
}

@media (min-width: 1200px) {
  .drugs-item__top::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    padding-top: 100%;
    border-radius: 50%;
    background-color: #39434d;
    z-index: -1;
  }
}

.drugs-item__top::after {
  content: '';
  position: absolute;
  top: 24px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: calc(100% - 48px);
  padding-top: calc(100% - 48px);
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
}

@media (min-width: 1200px) {
  .drugs-item__top::after {
    top: 15px;
    width: calc(100% - 30px);
    padding-top: calc(100% - 30px);
    transform: translate(-50%, 0);
  }
}

.drugs-item__top-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 3.4375rem;
  line-height: 1.15;
  letter-spacing: -0.075em;
  transform: translate(-50%, -50%);
  z-index: 2;
}

@media (min-width: 1200px) {
  .drugs-item__top-text {
    font-size: 4.5rem;
    transform: translate(-50%, -25%);
  }
}

.drugs-item--highlight {
  background-color: #8c2c3a;
  border-bottom-color: #c3283f;
  color: #fff;
}

.drugs-item--highlight::after {
  border-top-color: #c3283f;
}

.drugs-features {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: -8px;
  margin-right: -8px;
  margin-bottom: -1.25rem;
