}

@media (min-width: 900px) {
  .drugs-features::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 0;
    height: 85px;
    border-left: 10px solid #eee;
  }
}

@media (min-width: 900px) {
  .drugs-features::after {
    content: '';
    position: absolute;
    top: 85px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 75%;
    height: 0;
    border-top: 10px solid #eee;
  }
}

.drugs-features__item {
  position: relative;
  width: 100%;
  margin-bottom: 1.25rem;
  padding-left: 8px;
  padding-right: 8px;
  text-align: center;
}

@media (min-width: 900px) {
  .drugs-features__item {
    width: 25%;
    padding-top: 65px;
  }
}

@media (min-width: 900px) {
  .drugs-features__item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 0;
    height: 75px;
    border-left: 10px solid #eee;
    z-index: -1;
  }
}

.drugs-features__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 118px;
  height: 118px;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  background-color: #8c2c3a;
}

@media (min-width: 1440px) {
  .drugs-features__icon {
    width: 160px;
    height: 160px;
    margin-bottom: 2.5rem;
  }
}

.drugs-features__icon svg {
  transform: scale(0.75);
}

@media (min-width: 1440px) {
  .drugs-features__icon svg {
    transform: scale(1);
  }
}

.drugs-features__item:nth-child(2n) .drugs-features__icon {
  background-color: #39434d;
}

.drugs-features__text {
  text-transform: none;
  overflow-wrap: break-word;
}

@media (min-width: 1440px) {
