/* ==========================================================================
   #RESEARCH
   ========================================================================== */
.research {
  padding-top: 3.125rem;
  padding-top: 6.25rem;
}

.research__title {
  margin-bottom: 4rem;
}

.research-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.research-col {
  padding-left: 15px;
  padding-right: 15px;
}

.research-col--left {
  width: 100%;
  width: 60%;
}

.research-col--right {
  width: 100%;
  width: 40%;
}

.research-chart__wrapper {
  text-transform: none;
  font-weight: 600;
}

.research-chart {
  width: 690px;
  height: 530px;
}

.research-chart__labels {
  display: flex;
}

.research-chart__labels > * {
  flex-grow: 1;
  text-align: center;
}

.research-chart__labels > *::before {
  content: none;
  margin-right: 20px;
}

.research-chart__labels > *:first-child::before {
  background-color: #c3283f;
}

.research-chart__labels > *:last-child::before {
  background-color: #39434d;
}

.research-chart__desc {
  text-align: center;
}

.research-list {
  font-size: 2rem;
  text-transform: none;
  line-height: 1.5;
}

.research-list > * + * {
  margin-top: 87px;
}

/* ==========================================================================
   #LINKS
   ========================================================================== */
.links {
  padding-bottom: 0.625rem;
}

@media (min-width: 1200px) {
  .links {
    padding-bottom: 3.375rem;
  }
}

.links__list {
  width: 100%;
  max-width: 1260px;
  margin-left: auto;
  margin-right: auto;
}

.links-item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.links-item__toggler {
  position: relative;
  display: block;
  width: 100%;
  padding: 1.25rem 0;
  border: none;
  font-size: 1rem;
  text-align: left;
  text-transform: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.links-item__toggler:hover, .links-item__toggler:focus {
  color: #8c2c3a;
}

@media (min-width: 600px) {
  .links-item__toggler {
    font-size: calc(1rem + 0.833vw);
  }
}

@media (min-width: 900px) {
  .links-item__toggler {
    padding: 1.875rem 1.25rem;
  }
}

@media (min-width: 1440px) {
  .links-item__toggler {
    padding: 2.5rem 1.25rem;
    font-size: 1.75rem;
  }
}

.links-item__toggler.is-active .links-item__icon-bar {
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.links-item__icon {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
  pointer-events: none;
}

.links-item__icon path {
  fill: currentColor;
}

.links-item__content {
  visibility: hidden;
  opacity: 0;
  height: 0;
  overflow: hidden;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  transition: visibility 0.2s ease-in-out, opacity 0.2s ease-in-out, height 0.2s ease-in-out;
}

.links-item__content.is-open {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/* ==========================================================================
   #FOOTER
   ========================================================================== */
.footer {
  text-transform: none;
}

.footer-top {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  font-size: 1.125rem;
}

@media (min-width: 1440px) {
  .footer-top {
    padding-top: 2.875rem;
    padding-bottom: 2.875rem;
