  flex-wrap: wrap;
  align-items: center;
}

@media (max-width: 599.98px) {
  .footer-top__row {
    flex-direction: column;
    text-align: center;
  }
}

.footer-mid {
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  background-color: #39434d;
  color: #fff;
  text-align: center;
  padding-top: 1.5625rem;
  padding-bottom: 1.125rem;
}

.footer-mid__info {
  font-weight: bold;
  font-size: 1.375rem;
  text-transform: uppercase;
}

@media (min-width: 1440px) {
  .footer-mid__info {
    font-size: 1.875rem;
  }
}

.footer-bottom {
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  font-size: 1rem;
}

@media (max-width: 599.98px) {
  .footer-bottom {
    text-align: center;
  }
}

@media (min-width: 1440px) {
  .footer-bottom {
    padding-top: 3.75rem;
    padding-bottom: 4.75rem;
    font-size: 1.25rem;
  }
